import React, {useEffect,useState} from 'react'
import './App.css'
import socketIOClient from "socket.io-client";
const ENDPOINT = "http://127.0.0.1:8000";

function App(){
  const [streamSize, setStreamSize] = useState(2)
  const [nowPressed, setNowPressed] = useState('SPACE')
  const [response, setResponse] = useState("")

  const socket = socketIOClient(ENDPOINT);
  useEffect(()=>{
    document.addEventListener("keydown", keyboardPress, false);
    socket.on('connect', function() {
      console.log('client connected');
    });

    return () => socket.disconnect();

  },[])

  const keyboardPress = (event) => {
    switch(event.keyCode){
      case 87: setNowPressed('W');break;
      case 83: setNowPressed('S');break;
      case 65: setNowPressed('A');break;
      case 68: setNowPressed('D');break;
      case 32: setNowPressed('SPACE');break;
      case 81: setNowPressed('Q');break;
      case 69: setNowPressed('E');break;
      case 90: setNowPressed('Z');break;
      case 67: setNowPressed('C');break;
      case 73: setNowPressed('I');break;
      case 66: setNowPressed('B');break;
    }
  }

  useEffect(()=>{
    const buttons = [document.getElementById('W').style,
                    document.getElementById('S').style,
                    document.getElementById('A').style,
                    document.getElementById('D').style,
                  
                    document.getElementById('SPACE').style,
                  
                    document.getElementById('Q').style,
                    document.getElementById('E').style,
                    document.getElementById('Z').style,
                    document.getElementById('C').style,
                    document.getElementById('I').style,
                    document.getElementById('B').style,
                    ]
    
    buttons.map(item=>{item.backgroundColor="";item.color=""})
    switch(nowPressed){
      case 'W':buttons[0].backgroundColor="#222";buttons[0].color="#fff";break;
      case 'S':buttons[1].backgroundColor="#222";buttons[1].color="#fff";break;
      case 'A':buttons[2].backgroundColor="#222";buttons[2].color="#fff";break;
      case 'D':buttons[3].backgroundColor="#222";buttons[3].color="#fff";break;
      case 'SPACE':buttons[4].backgroundColor="#222";buttons[4].color="#fff";break;
      case 'Q':buttons[5].backgroundColor="#222";buttons[5].color="#fff";break;
      case 'E':buttons[6].backgroundColor="#222";buttons[6].color="#fff";break;
      case 'Z':buttons[7].backgroundColor="#222";buttons[7].color="#fff";break;
      case 'C':buttons[8].backgroundColor="#222";buttons[8].color="#fff";break;
      case 'I':buttons[9].backgroundColor="#222";buttons[9].color="#fff";break;
      case 'B':buttons[10].backgroundColor="#222";buttons[10].color="#fff";break;
    }
    
    socket.emit("data",{'key':nowPressed})
  },[nowPressed])

  const higherSize = () => {
    if(streamSize<6)setStreamSize(streamSize+1,changeSize)
  }

  const lowerSize = () => {
    if(streamSize>0)setStreamSize(streamSize-1,changeSize)
  }

  useEffect(()=>{
    changeSize()
  },[streamSize])

  const changeSize = () => {
    const streamStyle = document.getElementById('stream').style
    switch(streamSize){
      case 0:
        streamStyle.width='160px'
        streamStyle.height='120px'
        break
      case 1:
        streamStyle.width='320px'
        streamStyle.height='240px'
        break
      case 2:
        streamStyle.width='640px'
        streamStyle.height='480px'
        break
      case 3:
        streamStyle.width='800px'
        streamStyle.height='600px'
        break
      case 4:
        streamStyle.width='1024px'
        streamStyle.height='768px'
        break
      case 5:
        streamStyle.width='1280px'
        streamStyle.height='962px'
        break
      case 6:
        streamStyle.width='2048px'
        streamStyle.height='1536px'
        break
      default:
        streamStyle.width='640px'
        streamStyle.height='480px'
        break
    }
  }

    return (
      <div className="App">
        <div className='stream-div'>
          <div className='stream-size'>
            <button onClick={()=>lowerSize()} className='size-button'>-</button>
            <button onClick={()=>higherSize()} className='size-button'>+</button>
          </div>
          <img id="stream" src="stream.mjpg"/>
        </div>
        <div className='controls'>
          <div className='controls-directions'>
            <div className='controls-directions-row'>
              <button id="Q" className='constrols-button bblue '><div className="rot25">ðŸ –</div><div className='button-description'>Q</div></button>
              <button id="W" className='constrols-button byellow '><div className="rot3">ðŸ –</div><div className='button-description'>W</div></button>
              <button id="E" className='constrols-button bblue '><div className="rot35">ðŸ –</div><div className='button-description'>E</div></button>
            </div>
            <div className='controls-directions-row'>
              <button id="A" className='constrols-button bgray'><div className="rot2">ðŸ –</div><div className='button-description'>A</div></button>
              <button id="S" className='constrols-button byellow'><div className="rot1">ðŸ –</div><div className='button-description'>S</div></button>
              <button id="D" className='constrols-button bgray'><div className="rot">ðŸ –</div><div className='button-description'>D</div></button>
            </div>
            <div className='controls-directions-row'>
              <button id="Z" className='constrols-button bblue '><div className="rot15">ðŸ –</div><div className='button-description'>Z</div></button>
              <button  id="C" className='constrols-button bblue '><div className="rot05">ðŸ –</div><div className='button-description'>C</div></button>
            </div>
            <div className='controls-directions-row'>
              <button id="SPACE" className='constrols-button bred bwide3'>STOP<div className='button-description'>Space</div></button>
            </div>
          </div>

          <div className='controls-standup'>
            <div className='controls-directions-row'>
              <button id="I" className='constrols-button bwhite bwide3'>Stand Up - FRONT<div className='button-description'>I</div></button>
            </div>
            <div className='controls-directions-row'>
              <button id="B" className='constrols-button blblue bwide3'>LIGHT<div className='button-description'>B</div></button>
            </div>
          </div>
        </div>
        <div className='pressed-now'>
          {nowPressed}
        </div>
      </div>
    )
}

export default App
